@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 13px 32px;
  z-index: 1;

}

.dropdown:hover .dropdown-content {
  display: block;
}

.bespoke3d {
  background-color: #f2f2f2;
  background-size: cover;
}

.btl-ems {
  background-color: #fff;
  padding: 80px 15px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.contouring-devices {
  background-color: #133D6A !important;
  background-size: cover;
}

input[type=text] {
  width: 70%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 2px solid #f3f3f3;
  border-radius: 24px;
  position: relative;
  left: 50px;
}

input[type=email] {
  width: 70%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 2px solid #f3f3f3;
  border-radius: 24px;
  position: relative;
  left: 53px;
}

input[type=date] {
  width: 70%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 2px solid #f3f3f3;
  border-radius: 24px;
  position: relative;
  left: 52px;
}

textarea {
  width: 70%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 2px solid #f3f3f3;
  border-radius: 24px;
  position: relative;
  left: -17px;
  top: 41px;
}

.block {
  display: block;
  width: 30%;
  border: none;
  border-radius: 24px;
  background-color: #4CAF50;
  color: white;
  padding: 14px 28px;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
  position: relative;
  top: 43px;
  left: 151px;
}

.block:hover {
  background-color: #ddd;
  color: black;
}

.blockButton {
  display: block;
  /* width: 30%; */
  border: none;
  border-radius: 24px;
  background-color: #4CAF50;
  color: white;
  padding: 14px 28px;
  font-size: 16px;
  cursor: pointer;
  text-align: center;
  position: relative;
  top: 43px;
  left: 30%;
  /* left: 151px; */
}

.blockButton:hover {
  background-color: #ddd;
  color: black;
}

#header {
  background-color: #8757bd!important;
  background-size: cover;
  background-repeat: no-repeat;
}


#footer {
  background-color: #133D6A!important;
  background-size: cover;
  background-repeat: no-repeat;
}

.bus-hour {
  background-color: #133D6A;
  background-size: cover;
  position: relative;
  width: 75%;
}

.bus-hour hr {
  width: 100%;
  border: 1.2px solid #ffffff;
}

.bus-hour h4 {
  color: #ffffff;
  position: relative;
  left: 11%;
}

.popupcontent{
  text-align: center;
}




.modal {
  font-size: 12px;
}
.modal > .header {
  width: 100%;
  border-bottom: 1px solid gray;
  font-size: 18px;
  text-align: center;
  padding: 5px;
}
.modal > .content {
  width: 100%;
  padding: 10px 5px;
}
.modal > .actions {
  margin: auto;
}
.modal > .actions {
  width: 100%;
  padding: 10px 5px;
  text-align: center;
}
.modal > .close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}





@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}





@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

